import '../App.css'
import TextCircleButton from './UI/TextCircleButton/TextCircleButton';

function BlogItem(props) {

    return (
        <div className="homepageBlogItem">
            <p className='homepageBlogItemDate'>{props.date}</p>
            <p className='homepageBlogItemTitle'>{props.title}</p>
            <p className='homepageBlogItemDesc'>{props.description}</p>
            <TextCircleButton link={props.link} buttonText={'Read All'} />
        </div>
    )
}

export default BlogItem; 