import '../App.css'
import ContactUs from '../components/ContactUs';
import Footer from '../components/Footer';
import ProductGraphItem from '../components/ProductGraphItem';
import ProductsListItem_2 from '../components/ProductsListItem_2'

function Cart() {

    var sampleProduct = [
        {
            "image": "../images/pushpak.png",
            "name": "KPT ECP 1",
            "description": "No Hydraulic Lift E-Cart"
        },
        {
            "image": "../images/e-rickshaw.png",
            "name": "KPT ECP 2",
            "description": "No Hydraulic Lift E-Cart"
        },
        {
            "image": "../images/pushpak.png",
            "name": "KPT ECP 3",
            "description": "No Hydraulic Lift E-Cart"
        },
        {
            "image": "../images/e-rickshaw.png",
            "name": "KPT ECP 4",
            "description": "No Hydraulic Lift E-Cart"
        },
    ]


    var specifications = [
        {
            title: "Battery",
            description: "12V 100 Ah X 6 Nos"
        },
        {
            title: "Motor",
            description: "Brushless DC Motor 1800 W"
        },
        {
            title: "Overall Dimensions",
            description: "2800 X 940 X 1290 mm"
        },
        {
            title: "Loading Capacity",
            description: "310 Kg"
        },
        {
            title: "Front Tyre",
            description: "4.50-10, 8 ply Outer Diameter 505 X width 127 mm"
        },
        {
            title: "Rear Tyre",
            description: "165D 13, 8 ply Outer Diameter 600 X width 165 mm"
        },
        {
            title: "Cargo Box - Open type",
            description: "1250 X 950 X 500 mm"
        },
        {
            title: "Cargo Box - Close type",
            description: "1250 X 950 X 1000 mm"
        },

        {
            title: "Cargo Box Lifting",
            description: "Hydraulic"
        },
        {
            title: "Climbing Ability",
            description: "10 to 30 degree"
        },
        {
            title: "Charging Time",
            description: "6 to 8 hours"
        },
        {
            title: "Running Time",
            description: "10 to 12 hours"
        },
        {
            title: "Driving Mode",
            description: "Shaft driving"
        },
        {
            title: "Standard Accessories",
            description: "Charger, Battery, Meter, Light & Horn"
        },
        {
            title: "Speed",
            description: "15 to 25 kmph"
        },
        {
            title: "Charging System",
            description: "220 Vac amp."
        },

        {
            title: "Ground Clearance",
            description: "300 mm"
        },
        {
            title: "Sitting arrangement",
            description: "Extra person beside operator of dumper"
        }
    ]

    return (
        <div className="productsBody">
            <div className="container">
                <div className="productHeader">
                    <div className="productsBodyNavigation">
                        <p><span>Home</span>  <span>/</span> <span>Products</span> <span>/</span> <span className='activeLink'>KPT E-Rikshaw</span></p>
                    </div>
                    <div className="productHeaderBody">
                        <div className="productHeaderBodyLeft">
                            <p className='siteTitle productHeaderTitle'>KPT E-Rikshaw</p>
                            <div className="productHeaderCategory">
                                <p className="productHeaderCategoryName">Passenger</p>
                            </div>
                            <div className="productHeaderData">
                                <p className='verticalText'>specification</p>
                                <div className="productHeaderDataBody">
                                    <p className='carDesc'>Lorem ipsum dolor sit amet consectetur adipisicing elit. Maxime mollitia,
                                        molestiae quas vel sint commodi repudiandae consequuntur voluptatum laborum
                                        numquam blanditiis harum quisquam eius sed odit fugiat iusto fuga praesentium
                                        optio, eaque rerum!</p>
                                    <p className='carDesc'>Provident similique accusantium nemo autem. Veritatis
                                        obcaecati tenetur iure eius earum ut molestias architecto voluptate aliquam
                                        nihil, eveniet aliquid culpa officia aut! Impedit sit sunt quaerat, odit,
                                        tenetur error, harum nesciunt ipsum debitis quas aliquid.</p>
                                </div>
                            </div>
                        </div>
                        <div className="productHeaderBodyRight">
                            <div className="productHeaderImage imageCarWrapper">
                                <img className='productHeaderItemImage imageCar' src="/images/pushpak.png" alt="Product" />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="productDimensions productCarDimentions">
                    <p className='verticalText'>Specifications</p>
                    <div className="productDimensionsBody">
                        <p className="siteTitle productDimensionsTitle">Specifications</p>
                        <hr />
                        <div className="productCarSpecWrapper">
                        <iframe width="100%" height="550" src="https://www.youtube.com/embed/Tndd52E5p0Q" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                        </div>

                        <div className="productDimensionsList cartDimension">
                            {
                                specifications.map((item) => (
                                    <ProductGraphItem title={item.title} description={item.description} />
                                ))
                            }

                        </div>
                    </div>
                </div>
                <div className="productCurve">
                    <p className='verticalText'>meter scheme</p>
                    <div className="productCurveBody">
                        <p className="siteTitle productCurveBodyTitle">Performance Curve</p>
                        <hr />
                        <img className='productHeaderItemImage' src="images/curve.svg" alt="Product" />
                        <div className="productCurveList">
                            <ProductGraphItem title={'Q1'} subtitle={'Minimum Flowrate '} description={'0,025 & 0,0156 m3/h'} />
                            <ProductGraphItem title={'Q2'} subtitle={'Transitional Flowrate '} description={'0,040 & 0,025 m3/h '} />
                            <ProductGraphItem title={'Q3'} subtitle={'Permanent Flowrate'} description={'2,5 m3/h '} />
                            <ProductGraphItem title={'Q4'} subtitle={'Overload Flowrate '} description={'3,125 m3/h'} />
                        </div>
                    </div>
                </div>
                <div className="similarProducts">
                    <p className='verticalText'>MODELS</p>
                    <div className="similarProductsBody">
                        <p className="siteTitle similarProductsBodyTitle">Models</p>
                        <div className="similarProductsList modelsBlock">
                            {
                                sampleProduct.map((item, index) => (
                                    <ProductsListItem_2 key={index} productData={item} />
                                ))
                            }
                        </div>
                    </div>
                </div>
            </div>

            <div className="container">
                <ContactUs />
            </div>
            <Footer />
        </div>

    )
}

export default Cart;