import '../App.css'

function ProductsListItem_2(props) {

    const itemData = props.productData;

    return (
        <div className='productsListItemBlock'>
            <div className="productListItemImage">
                <img src={itemData.image} alt="Item Background" />
            </div>
            <p className="productListItemTitle title2">{itemData.name}</p>
            <p className="productListItemDesc">{itemData.description}</p>
        </div>
    )
}

export default ProductsListItem_2;