import '../App.css'
import ArrowLink from './UI/ArrowLink/ArrowLink';

function ProductsListItem(props) {

    const itemData = props.productData;

    return (
        <div className='productsListItemBlock'>
            <div className="productListItemImage">
                <img src={itemData.image} alt="Item Background" />
            </div>
            <p className="productListItemTitle">{itemData.name}</p>
            <ArrowLink link={'/'} linkText={'View Product'} />
        </div>
    )
}

export default ProductsListItem;