import React, { Component } from 'react';
import Fade from 'react-reveal/Fade';

function Advantages() {

    return (
        <div className="homepageAdvCards">
            <Fade bottom>
                <div className="homepageAdvCard">
                    <img src="images/advantagesCards/flexible.svg" alt="Icon1" />
                    <p className="siteTitle homepageAdvCardTitle">Flexible payment</p>
                    <p className="homepageAdvDesc">Flexible payment options increase your customers purchasing power.</p>
                </div>
            </Fade>
            <Fade bottom>
            <div className="homepageAdvCard">
                <img src="images/advantagesCards/smart.svg" alt="Icon" />
                <p className="siteTitle homepageAdvCardTitle">Smart Technology</p>
                <p className="homepageAdvDesc">Construction business owners are facing an unprecedented time in the construction industry.</p>
            </div>
            </Fade>
            
            <Fade bottom>
            <div className="homepageAdvCard">
                <img src="images/advantagesCards/staff.svg" alt="Icon" />
                <p className="siteTitle homepageAdvCardTitle">Experienced Staff</p>
                <p className="homepageAdvDesc">Flexible payment options, individual approach tExperienced professionals know what to do.</p>
            </div>
            </Fade>
            
            <Fade bottom>
            <div className="homepageAdvCard">
                <img src="images/advantagesCards/innovative.svg" alt="Icon" />
                <p className="siteTitle homepageAdvCardTitle">Always Innovative</p>
                <p className="homepageAdvDesc">Use your creativity to think of better ways to do company processes. What things can you streamline.</p>
            </div>
            </Fade>
            
            <Fade bottom>
            <div className="homepageAdvCard">
                <img src="images/advantagesCards/products.svg" alt="Icon" />
                <p className="siteTitle homepageAdvCardTitle">Well-known Products</p>
                <p className="homepageAdvDesc">In the global competitive market, most companies keep inventing new products that enable them to remain relevant.</p>
            </div>
            </Fade>
            
        </div>
    )
}

export default Advantages;