import './ArrowLink.css'
import {Link} from 'react-router-dom'

function ArrowLink(props) {

    return (
        <Link className='arrowLink' to={'/product'}>{props.linkText} <svg width="16" height="8" viewBox="0 0 16 8" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path className='arrowLinkIcon' d="M12.01 3H0V5H12.01V8L16 4L12.01 0V3Z" fill="#155EFF" />
        </svg></Link>
    )
}

export default ArrowLink;