import './App.css';
import Menu from './components/Menu';
import Homepage from './pages/Homepage';
import ProductPage from './pages/ProductPage';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import Products from './pages/Products';
import About from './pages/About';
import Announcements from './pages/Announcements'
import Financial from './pages/Financial'
import {useState} from 'react';
import Investors from './pages/Investors';
import ElectricVehicles from './pages/ElectricVehicles';
import Cart from './pages/Cart';

function App() {

  const [activeCategory, setActiveCategory] = useState('')



  return (
    <div className="App">
      <BrowserRouter>
        <Menu setActiveCategory={setActiveCategory} activeCategory={activeCategory} />
        <Routes>
          <Route path="/" element={<Homepage />} />
          <Route path="/products" element={<Products />} />
          <Route path='/product' element={<ProductPage />} />
          <Route path='/about' element={<About />} />
          <Route path='/announcements' element={<Announcements />} />
          <Route path='/financial-results' element={<Financial />} />
          <Route path='/investor-contact' element={<Investors />} />
          <Route path='/electric-vehicles' element={ <ElectricVehicles />} />
          <Route path='/electric-vehicles/cart' element={ <Cart />} />
        </Routes>
      </BrowserRouter>

    </div>
  );
}

export default App;
