import React, { useState } from "react";
import '../App.css'


function ContactUs() {

    return (
        <div className="contactBlock">
            <p className='verticalText'>request call</p>
            <div className="contactBLockContent">
                <div className="contactBlockContentText">
                    <p className='siteTitle contactTitle'>Contact <span>Us</span></p>
                    <p className="contactBlockText">Interested in telecommunication equipment? Write to us!</p>
                    <p className="contactBlockTextTitle">Our Office:</p>
                    <p className="contactBlockText">12411 W Fielding Circle, Los Angeles, 90094 CA, USA</p>
                    <p><a href="mailto:info@smarttechnomash.uz">info@smarttechnomash.uz</a></p>
                </div>
                <div className="contactBlockContentForm">
                    <input type="text" name="fullName" id="fullName" placeholder="Full Name" />
                    <input type="email" name="emailAdress" id="emailAdress" placeholder="Email" />
                    <textarea name="messageBox" id="messageBox" placeholder="Text your message here"></textarea>
                    <div className="contactBlogSend">
                        <p className="contactBlogSendText">SEND MESSAGE</p>
                        <div className="contactBlockSendButton">
                            <svg width="16" height="8" viewBox="0 0 16 8" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M12.01 3L0 3L0 5L12.01 5L12.01 8L16 4L12.01 0V3Z" fill="#FAFAFA" />
                            </svg>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    )
}

export default ContactUs; 