import React from 'react';
import '../App.css'

function ProductAdvantages(props) {

    return (
        <div className='productAdvantageItem'>
            <p className="siteTitle productAdvantagesItemTitle">{props.number}</p>
            <p className="siteTitle productAdvantagesItemSubtitle">{props.title}</p>
            <p className="productAdvantagesItemDescription">{props.description}</p>
        </div>
    )
}

export default ProductAdvantages;