import React from 'react';
import '../App.css'
import ContactUs from '../components/ContactUs';
import Footer from '../components/Footer';
import Production from '../components/Production';
import Directors from '../directors.json'
import FilesList from '../components/FilesList'
import { GoogleMap, useLoadScript, Marker } from '@react-google-maps/api'
import Fade from 'react-reveal/Fade';

function About() {

    const { isLoaded } = useLoadScript({ googleMapsApiKey: "AIzaSyD1lq8dFnl_eV11FOVVLqZ0jpuENqPz26U" })
    if (!isLoaded) return <div>Loading...</div>



    return (
        <div>
            <div className="aboutBody">
                <div className="productsBodyNavigation">
                    <p><span>Home</span>  <span className='linksSeperator'>/</span> <span className='activeLink'>About Us</span></p>
                </div>
                <Fade left><p className='siteTitle aboutBodyTitle'>About <br /><span>SmartTechnoMash</span></p></Fade>

                <div className="homepageAbout">
                    <p className='verticalText'>smarttechnomash</p>
                    <div className="homepageAboutBody">
                        <p className='siteTitle homepageAboutTitle'>General <span>Information</span></p>
                        <Fade bottom>
                            <div className="homepageAboutText">
                                <div>
                                    <p><span>SmartTechnoMash</span> has begun to work in 2007 at fixing, maintaining and calibrating both water and electrical meter. STM has pioneering Azerbaijan water and electircity meter sector with high quality.</p>
                                    <p>As being SmartTechnoMash, we aim to make our brand sustainable pioneering and unique trademark with manufacturing %100 domestic water meter, heat meter and energy meter to our customers without sacrificing quality with also many years of experiences.</p>
                                </div>
                                <div>
                                    <p>The journey initiated by our founder Salvatore Ganacci with the dream of creating a national Azerbaijan brand in a small workshop, is carried to the future by his sons Salvatore and Ganacci brothers while protecting the values and corporate goals of the first day. <span>SmartTechnoMash</span> branded meters and integrated systems are safely used in thousands of projects in over 60 countries.</p>
                                    <p>That first production plant was based on 300 sqm closed area and 300 pieces of water meters were being produced per day.</p>
                                    <p>Our company is presently producing with high technology in two different plants located at Baku Industrial Zone, STM on 21.000 m2 closed area. The third and the latest factory having 50.000 m2 area has also been established within second quarter of 2014 and it is currently active.We have a staff of 800 people in total consisting of 40 engineers, technicians and workers who are specialized on their own professions. Baylan has the capacity to produce 15.000 pcs of water meters per day, and presently exporting its manufactured goods to different countries on all over the world. This fact has made SmartTechnoMash an international and a well-known company in the world.</p>
                                </div>
                            </div>
                        </Fade>

                    </div>
                </div>
                <div className="aboutBodyMediaContainer">
                    <img src="images/aboutMedia.png" alt="Media" />
                </div>

                <div className="aboutBodyProduction">
                    <p className='verticalText'>company’s Activities</p>
                    <div>
                        <p className="siteTitle aboutBodyProductionTitle"><span>Board</span> of Directors</p>
                        <p className='aboutBodyProductionDesc'>The company's activities can be divided into <span>4 main product areas:</span> gas, water,electric meters and repair parts.</p>
                        <Fade bottom>
                            <div className="aboutBodyList">
                                {
                                    Directors.map((item) => (
                                        <Production title={item.name} role={item.role} description={item.profession} />
                                    ))
                                }
                            </div>
                        </Fade>

                    </div>
                </div>
            </div>
            <div className="aboutBodyLocation">
                <div className="aboutLocationBody">
                    <p className='verticalText'>worldwide location</p>
                    <div>
                        <p className="siteTitle aboutBodyProductionTitle">Geographical <span>Locations</span></p>
                        <p className='aboutBodyProductionDesc'>Products are supplied to Eastern and Western Europe, Scandinavian countries, Middle East, North and South America, CIS and Asian countries.</p>
                        <div className="aboutBodyLocationStats">
                            <div>
                                <p className='siteTitle aboutBodyLocStatsTitle'>2</p>
                                <p className="aboutBodyLocStatsDesc">Continents</p>
                            </div>
                            <div>
                                <p className='siteTitle aboutBodyLocStatsTitle'>10</p>
                                <p className="aboutBodyLocStatsDesc">Countries</p>
                            </div>
                            <div>
                                <p className='siteTitle aboutBodyLocStatsTitle'>18</p>
                                <p className="aboutBodyLocStatsDesc">Cities</p>
                            </div>
                        </div>
                        <div className="aboutBodyLocationText">
                            <Fade left>
                                <p>The geographical location of production sites and logistics organized in a seamless way substantially expand business opportunities and allow the company to plan deliveries to different areas with the maximum efficiency.</p>
                            </Fade>
                            <div>
                                <Fade right>
                                    <p>The company cooperates with leading international tire companies and manufacturers of industrial rubber products, plastics, paints and other products made using carbon black.</p>
                                    <p><span>SmartTechnoMash</span> manufactures water meters  in the Azerbaijan and in the Russian Federation.</p>
                                </Fade>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="aboutBodyMapImageContainer">
                    <img src="/images/map.svg" alt="Map" />
                </div>

            </div>
            <div className="aboutBodyServices">
                <div>
                    <p className='verticalText'>worldwide location</p>
                    <div>
                        <p className="siteTitle aboutBodyServicesTitle">Confirmation of our <span>high quality services</span> </p>
                        <div className="aboutBodyServicesList">
                            <FilesList link={'/'} date={'19 March, 2023'} description={'EAC Declaration of Conformity Electromagnetic'} />
                            <FilesList link={'/'} date={'19 March, 2023'} description={'EAC Declaration of Conformity Electromagnetic'} />
                            <FilesList link={'/'} date={'19 March, 2023'} description={'EAC Declaration of Conformity Electromagnetic'} />
                            <FilesList link={'/'} date={'19 March, 2023'} description={'EAC Declaration of Conformity Electromagnetic'} />
                            <FilesList link={'/'} date={'19 March, 2023'} description={'EAC Declaration of Conformity Electromagnetic'} />
                        </div>
                    </div>
                </div>

            </div>
            <div className="aboutBodyWrapper">
                <div className="container">
                    <div className="aboutBodyVisit">
                        <p className='verticalText'>worldwide location</p>
                        <div className='aboutBodyContainer'>
                            <div className="aboutBodyVisitInfo">
                                <p className="siteTitle aboutBodyServicesTitle">Visit our <span>showroom</span></p>
                                <Fade bottom>
                                <div className="aboutBodyVisitData">
                                    <div>
                                        <p className="aboutBodyVisitTitle">Adress:</p>
                                        <p className="aboutBodyVisitDesc">100016, Republic of Uzbekidtan, Tashkent city, Yashnabad district, Aviasiozlar-1, 119/2</p>
                                    </div>
                                    <div>
                                        <p className="aboutBodyVisitTitle">Phone Number:</p>
                                        <p className="aboutBodyVisitDesc">+ 994 55 555 55 55</p>
                                    </div>
                                    <div>
                                        <p className="aboutBodyVisitTitle">Email:</p>
                                        <p className="aboutBodyVisitDesc"><a href="mailto:info@smart.com">info@smart.com</a></p>
                                    </div>
                                </div>
                                </Fade>
                            </div>
                        </div>
                    </div>

                </div>
                <Fade right>
                <div className="aboutBodyMap">
                    <div className='aboutBodyMapContainer'>
                        <GoogleMap zoom={10} center={{ lat: 44, lng: -80 }} mapContainerClassName="aboutBodyMapContainer"></GoogleMap>
                    </div>
                </div>
                </Fade>
            </div>
            <div className="container">
                <ContactUs />
            </div>

            <Footer />
        </div>
    )
}

export default About;