import '../App.css'
import CircleButton from './UI/CircleButton/CircleButton';

function HomepageProduct(props) {


    return (
        <div className="homepageProduct">
            <div className="homepageProductBody">
                <p>{props.title}</p>
                <CircleButton link={'#'} />
            </div>
        </div>
    )
}

export default HomepageProduct;