import React, { Component } from 'react';
import TextCircleButton from '../components/UI/TextCircleButton/TextCircleButton'
function FilesList(props) {
    return (
        <div className='fileList'>
            <div className="fileInfo">
                <img src="images/pdfLogo.svg" alt="Pdf Logo" />
                <p>PDF</p>
            </div>
            <p className='fileListDate'>{props.date}</p>
            <p className='fileListDescription'>{props.description}</p>
            <TextCircleButton link={props.link} buttonText={'READ ALL'} />
        </div>
    )
}

export default FilesList;