import '../App.css'
import HomepageProduct from '../components/HomepageProduct';
import BlogItem from '../components/BlogItem';
import ArrowLink from '../components/UI/ArrowLink/ArrowLink';
import FilledButton from '../components/UI/button/FilledButton';
import ContactUs from '../components/ContactUs'
import Footer from '../components/Footer'
import { Link, animateScroll as scroll } from "react-scroll";
import Advantages from '../components/Advantages';
import Fade from 'react-reveal/Fade';
function Homepage() {


    return (
        <div className="homepageBody">
            <div className="container">
                <div className="homepageHeader">
                    <div className="homepageHeaderInfo">
                        <p className='siteTitle homepageTitle'>Ready to use solutions for <span>data collection</span> in meter industry</p>
                        <p className='homepageHeaderDesc'>SmartTechnoMash® is Azerbaijan Engineering Company which is manufacturer of water, electric and gas equipments.</p>
                        <div className="homepageHeaderButtons">
                            <a href="#"><FilledButton buttonText={'Our Products'} /></a>
                            <button className='homepageHeaderDownload'><img src="images/icons/download.svg" alt="Download" /> PDF Presentation</button>
                        </div>
                        <div className="homepageHeaderDown">
                            <svg width="8" height="16" viewBox="0 0 8 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path className='homepageHeaderDownIcon' d="M5 12.01L5 -1.31134e-07L3 -2.18557e-07L3 12.01L-5.24974e-07 12.01L4 16L8 12.01L5 12.01Z" fill="#E8EBF2" />
                            </svg>
                            <Link className='homepageDownText' to="homepageAdv" smooth={true} duration={1000}>Scroll down</Link>
                        </div>
                    </div>
                    <div className="homepageHeaderImage">
                        <img src="images/manometr.png" alt="Manometr" />
                    </div>
                </div>

                <div className="homepageAdv">
                    <p className='verticalText'>our advantages</p>
                    <div className="homepageAdvContent">
                            <p className="siteTitle homepageAdvTitle">Proceed your <span>business</span> development with us</p>
                        <Advantages />
                    </div>
                </div>
                <div className="homepageProduction">
                    <p className='verticalText'>MAIN PRODUCTION AREAS</p>
                    <div className="homepageProductionBlock">
                            <p className='siteTitle homepageProductionTitle'>Production <span>Areas</span></p>
                            <p className="homepageProductDesc">The company's activities can be divided into 3 main product areas: gas, water and electric meters.</p>
                            <div className="homepageProductDescList">
                                <p>-  Blowers</p>
                                <p>-  Power Tools</p>
                                <p>-  E-Vehicles</p>
                            </div>
                            <ArrowLink link='#' linkText={'View all products'} />


                        <div className="homepageProductionList">
                            <Fade left><HomepageProduct title={'Blowers'} link={'#'} /></Fade>
                            <Fade bottom><HomepageProduct title={'Power Tools'} link={'#'} /></Fade>
                            <Fade right><HomepageProduct title={'E-Vehicles'} link={'#'} /></Fade>
                        </div>
                    </div>
                </div>
            </div>
            <div className="homepagePartnersContainer">
                <div className="homepagePartners">
                    <p className='verticalText'>our partners</p>
                    <div className="homepagePartnersBlock">
                        <p className='siteTitle homepageProductionTitle'>We work only with world's most <span>notable partners</span></p>
                        <p className="homepagePartnersDesc">We're a team of technologists with deep financial expertise, backed by some of the world's most notable investors.</p>
                        <Fade bottom>
                        <div className="homepagePartnersList">
                            <div className="homepagePartner">
                                <p className="siteTitle homepagePartnerTitle">Honeywell</p>
                                <p className="homepagePartnerDesc">(USA)</p>
                            </div>
                            <div className="homepagePartner">
                                <p className="siteTitle homepagePartnerTitle">KAS</p>
                                <p className="homepagePartnerDesc">(ITALY)</p>
                            </div>
                            <div className="homepagePartner">
                                <p className="siteTitle homepagePartnerTitle">Lumec</p>
                                <p className="homepagePartnerDesc">(Germany)</p>
                            </div>
                            <div className="homepagePartner">
                                <p className="siteTitle homepagePartnerTitle">Elsis</p>
                                <p className="homepagePartnerDesc">(POLAND)</p>
                            </div>
                            <div className="homepagePartner">
                                <p className="siteTitle homepagePartnerTitle">Elis PLZEN</p>
                                <p className="homepagePartnerDesc">(czech republic)</p>
                            </div>
                        </div>
                        </Fade>
                        
                    </div>

                </div>
            </div>
            <div className="container">
                <div className="homepageAbout">
                    <p className='verticalText'>our partners</p>
                    <div className="homepageAboutBody">
                        <p className='siteTitle homepageAboutTitle'>Creating your image in <span>digital world</span></p>
                        <div className="homepageAboutText">
                            <div>
                                <p><span>SmartTechnoMash</span> has begun to work in 2007 at fixing, maintaining and calibrating both water and electrical meter. STM has pioneering Azerbaijan water and electircity meter sector with high quality.</p>
                            </div>
                            <Fade right>
                            <div>
                                <p>The journey initiated by our founder Salvatore Ganacci with the dream of creating a national Azerbaijan brand in a small workshop, is carried to the future by his sons Salvatore and Ganacci brothers while protecting the values and corporate goals of the first day. <span>SmartTechnoMash</span> branded meters and integrated systems are safely used in thousands of projects in over 60 countries. That first production plant was based on 300 sqm closed area and 300 pieces of water meters were being produced per day. </p>
                                <ArrowLink link='#' linkText={'Link to the article'} />
                            </div>
                            </Fade>
                            
                        </div>
                    </div>
                </div>
            </div>
            <div className="homepageImage">
                <div className="homepageImageMask"></div>
                <img src="images/homepageImage.jpg" alt="Homepage" />
            </div>
            <div className="container">
                <div className="homepageBlog">
                    <p className='verticalText'>our partners</p>
                    <div className="homeBlogContent">
                        <p className="siteTitle homepageBlogTitle">Latest news from our <span>blog</span></p>
                        <Fade bottom>
                        <div className="homepageBlogList">
                            <BlogItem
                                link={'#'}
                                date={'04 March, 2020'}
                                title={'Installation of community water meters may be mandatory'}
                                description={'In September 2014, the Ministry of National Economy presented the draft law “On Amendments and Addenda to Some Legislative Acts of the Republic of Azerbaijan...'} />
                            <BlogItem
                                link={'#'}
                                date={'04 March, 2020'}
                                title={'Installation of community water meters may be mandatory'}
                                description={'In September 2014, the Ministry of National Economy presented the draft law “On Amendments and Addenda to Some Legislative Acts of the Republic of Azerbaijan...'} />
                            <BlogItem
                                link={'#'}
                                date={'04 March, 2020'}
                                title={'Installation of community water meters may be mandatory'}
                                description={'In September 2014, the Ministry of National Economy presented the draft law “On Amendments and Addenda to Some Legislative Acts of the Republic of Azerbaijan...'} />
                        </div>
                        </Fade>
                    </div>
                </div>
                <ContactUs />
            </div>
            <Footer />
        </div>

    )
}

export default Homepage;