import React from 'react';
import { Link } from 'react-router-dom'
import '../App.css'
import Footer from '../components/Footer'

function ElectricVehicles() {

    return (
        <div className='productsBody investorContainer'>
            <div className="container">
                <div className="productsBodyNavigation">
                    <p><span>Home</span> <span className='linksSeperator'>/</span><span className='activeLink'>KPT Electric Vehicles</span></p>
                </div>
                <div className="investorContactBody">
                    <p className="siteTitle productsBodyTitle electricVehiclesTitle">KPT <br/> <span>Electric Vehicles</span></p>

                    <div className="electricBody">
                        <div className="electricBodyBlock">
                            <div className="electricBodyBlockItem">
                                <Link to={'/electric-vehicles/cart'}>
                                    <img src="images/pushpak.png" alt="Car" />
                                    <p className="siteTitle">Pushpak E-Cart</p>
                                    <hr className='electricBodyItemLine' />
                                </Link>
                            </div>
                            <div className="electricBodyBlockItem">
                                <Link to={'#'}>
                                    <img src="images/e-rickshaw.png" alt="Car" />
                                    <p className="siteTitle">KPT E-Rikshaw</p>
                                    <hr className='electricBodyItemLine' />
                                </Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <Footer />
        </div>
    )
}

export default ElectricVehicles;