import './TextCircleButton.css'

function TextCircleButton(props) {


    return (
        <a className='textCircleButtonLink' href={props.link}>
            <p className="textCircleButtonText">{props.buttonText}</p>
            <div className='textCircleButton'>
                <svg width="9" height="14" viewBox="0 0 9 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path className='textCircleButtonIcon' d="M0.919922 1.74671L6.17326 7.00004L0.919922 12.2534L2.33326 13.6667L8.99992 7.00004L2.33326 0.333374L0.919922 1.74671Z" fill="white" />
                </svg>
            </div>
        </a>
    )
}

export default TextCircleButton;