import React, { useState } from "react";
import './hamburger.css'
import '../App.css'
import { Link } from 'react-router-dom'

function Menu(props) {

    const [activePage, setActivePage] = useState('Home')
    const [menuActive, setMenuActive] = useState(false)

    const [dropDownActive, setDropDownActive] = useState(false)

    function menuOpen() {
        setMenuActive(true)
    }
    function menuClose() {
        setMenuActive(false)
    }

    window.addEventListener('scroll', (event) => {
        setDropDownActive(false)
      });


function investorsHandler(e) {
    setActivePage('Investors')
    if (dropDownActive == false) {
        setDropDownActive(true)
    } else {
        setDropDownActive(false)
    }
}

    return (
        <div className="menuWrapper">
            <div className="container">
                <div className="menu">
                    <Link to={'/'}><img src="../images/logotype.svg" alt="Logotype" /></Link>
                    <a className="menuNumber" href="tel:+99455-555-55-55">
                        <img src="../images/icons/phone.svg" alt="Phone" />
                        <p>(994) 55-555-55-55</p>
                    </a>
                    <div className="menuLinks">
                        <ul>
                            <li><Link to='/' onClick={e => setActivePage('Home')} className={activePage === 'Home' ? 'menuLink menuLinkActive' : 'menuLink'} >Home <hr className="menuLinkLine" /></Link></li>
                            <li><Link to='/about' onClick={e => setActivePage('About Us')} className={activePage === 'About Us' ? 'menuLink menuLinkActive' : 'menuLink'} >About Us <hr className="menuLinkLine" /></Link></li>
                            <li><Link to='/products' onClick={e => setActivePage('Products')} className={activePage === 'Products' ? 'menuLink menuLinkActive' : 'menuLink'} >Products <hr className="menuLinkLine" /></Link></li>
                            <li className="menuInvestors">
                                <p onClick={investorsHandler} className={activePage === 'Investors' ? 'menuLink menuLinkActive' : 'menuLink'} >Investors ▼<hr className="menuLinkLine" /></p>
                                <div className={dropDownActive ? 'menuInvestorsDropdown menuInvestorsDropdownActive' : 'menuInvestorsDropdown'}>
                                    <div className="container">
                                        <ul>
                                            <Link onClick={e => setDropDownActive(false)}  to={'/announcements'}>Announcements</Link>
                                            <Link onClick={e => setDropDownActive(false)} to={'/financial-results'}>Financial Results</Link>
                                            <Link onClick={e => setDropDownActive(false)} to={'/investor-contact'}>Investor Contact</Link>
                                        </ul>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>

                    <div className="menuLang">
                        <img src="images/icons/Globe.svg" alt="Globe" />
                        <p>Rus</p>
                    </div>

                    <div className="menuHamburger">
                        <button className={menuActive ? 'hamburger hamburger--slider is-active' : 'hamburger hamburger--slider'} onClick={menuOpen} type="button">
                            <span class="hamburger-box">
                                <span class="hamburger-inner"></span>
                            </span>
                        </button>
                    </div>

                </div>
            </div>
            <div className={menuActive ? 'mobileMenu menuActive' : 'mobileMenu'}>
                <div className="mobileMenuHeader">
                    <img src="images/logotype.svg" alt="Logotype" />
                    <button className='hamburger hamburger--slider is-active' onClick={menuClose} type="button">
                        <span className="hamburger-box">
                            <span className="hamburger-inner"></span>
                        </span>
                    </button>
                </div>
                <div className="mobileMenuLinks">
                    <ul>
                        <li><Link to='/' className="menuLink">Home <hr className="menuLinkLine" /></Link></li>
                        <li><Link to='/about' className="menuLink">About Us <hr className="menuLinkLine" /></Link></li>
                        <li><Link to='/products' className="menuLink">Products</Link></li>
                        <li><Link to={'/announcements'} className="menuLink">Announcements</Link></li>
                        <li><Link to={'/financial-results'} className="menuLink">Financial Results</Link></li>
                        <li><Link to={'/investor-contact'} className="menuLink">Investor Contact</Link></li>
                    </ul>
                </div>
            </div>
        </div >
    )
}

export default Menu; 