import '../App.css'

function Production(props) {

    return (
        <div className='productionCard'>
            <p className="siteTitle productionCardTitle">{props.title}</p>
            <hr className='productionCardLine' />
            <p className="productionCardDesc">{props.description}</p>
            <p className="siteTitle productionCardDesc-2">{props.role}</p>
        </div>
    )
}

export default Production;