import '../App.css'
import ProductsListItem from './ProductsListItem';
import { useState } from 'react'

function ProductsList(props) {

    var categoryData = props.productData;

    const [listActive, setListActive] = useState(true)
    const [listOpenStatus, setListOpenStatus] = useState('-')

    function handleListActive() {
        if (listActive == true) {
            setListActive(false)
            setListOpenStatus('+')
        } else {
            setListActive(true)
            setListOpenStatus('-')
        }
    }

    return (
        <div className='productsListItem' onClick={handleListActive}>
            <div className="productsListHeader">
                <p className={listActive ? 'siteTitle productsListTitle productListTitleActive' : 'siteTitle productsListTitle'}>{categoryData.category}</p>
                <p className='siteTitle productListSpoilerState'>{listOpenStatus}</p>
            </div>
            <div className={listActive ? 'productsListBody' : 'productsListBody productsListBodyHidden'} >
                <p className='verticalText'>{categoryData.category}</p>
                <div className="productsItems">
                    {
                        categoryData.products.map((product, index) => (
                            <ProductsListItem key={index} productData={product} />
                        ))
                    }

                </div>
            </div>
        </div>
    )
}

export default ProductsList;