import '../App.css'
import ContactUs from '../components/ContactUs';
import Footer from '../components/Footer';
import ProductAdvantages from '../components/ProductAdvantages';
import ProductGraphItem from '../components/ProductGraphItem';
import FilesList from '../components/FilesList';
import ProductsListItem from '../components/ProductsListItem'

function ProductPage() {

    var sampleProduct = [
            {
                "image": "https://firebasestorage.googleapis.com/v0/b/kpt-web.appspot.com/o/blowers%2Ftwin-lobe%2FKTVR32.png?alt=media",
                "name": "KTVR32",
                "specification": "Tri Lobe"
            },
            {
                "image": "https://firebasestorage.googleapis.com/v0/b/kpt-web.appspot.com/o/blowers%2Ftwin-lobe%2FKSR900.png?alt=media",
                "name": "KSR900",
                "specification": "Twin Lobe"
            }
        ]


    return (
        <div className="productsBody">
            <div className="container">
                <div className="productHeader">
                    <div className="productsBodyNavigation">
                        <p><span>Home</span>  <span>/</span> <span>Products</span> <span>/</span> <span className='activeLink'>DOMINO – ARD 25 RW</span></p>
                    </div>
                    <div className="productHeaderBody">
                        <div className="productHeaderBodyLeft">
                            <p className='siteTitle productHeaderTitle'>DOMINO –  ARD 25 RW</p>
                            <div className="productHeaderCategory">
                                <p className="productHeaderCategoryName">Water Meter</p>
                                <p className="productHeaderModel">Model: #002095</p>
                            </div>
                            <div className="productHeaderData">
                                <p className='verticalText'>specification</p>
                                <div className="productHeaderDataBody">
                                    <p className='siteTitle'>Performance Data</p>
                                    <div className="productHeaderDataList">
                                        <div class="param">
                                            <span class="param__prop">Overload Flowrate, m3/h</span>
                                            <span class="param__value">3.125</span>
                                        </div>
                                        <div class="param">
                                            <span class="param__prop">Permanent Flowrate, m3/h</span>
                                            <span class="param__value">2.5</span>
                                        </div>
                                        <div class="param">
                                            <span class="param__prop">Transitional Flowrate, m3/h</span>
                                            <span class="param__value">0.040; 0.025</span>
                                        </div>
                                        <div class="param">
                                            <span class="param__prop">Mounting on the network</span>
                                            <span class="param__value">Horizontal</span>
                                        </div>
                                        <div class="param">
                                            <span class="param__prop">Initial Flow, I/h</span>
                                            <span class="param__value">5</span>
                                        </div>
                                        <div class="param">
                                            <span class="param__prop">Maximum Working Pressure, bar</span>
                                            <span class="param__value">16</span>
                                        </div>
                                        <div class="param">
                                            <span class="param__prop">Maximum Working Temperature, °C</span>
                                            <span class="param__value">50</span>
                                        </div>
                                        <div class="param">
                                            <span class="param__prop">Pressure Loss, bar</span>
                                            <span class="param__value">1</span>
                                        </div>
                                        <div class="param">
                                            <span class="param__prop">Maximum Registration Capacity</span>
                                            <span class="param__value">99999</span>
                                        </div>
                                        <div class="param">
                                            <span class="param__prop">Quantity per package</span>
                                            <span class="param__value">10</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="productHeaderBodyRight">
                            <div className="productHeaderImage">
                                <img className='productHeaderItemImage' src="https://i.imgur.com/pCcRqm7.png" alt="Product" />
                            </div>
                        </div>
                    </div>
                </div>
                <div className="productBodyAdvantages">
                    <p className='verticalText'>product advantages</p>
                    <div className="siteTitle productBodyAdvantagesContent">
                        <p className='productBodyAdvTitle'>Benefits and differences from competitors</p>
                        <p className='productBodyAdvDesc'>Easy to install; very high sensitivity even at low flows; special glass with high resistance to pressure and impact and allows you to build scalable applications , the KY-1 Single jet Wet type is the ideal solution for corporate applications.</p>
                        <div className="productBodyAdvantagesList">
                            <ProductAdvantages number={1} title={'High quality service'} description={'3 years of warranty with all type of maintance.'} />
                            <ProductAdvantages number={2} title={'Easy and smart'} description={'Single jet design assures very high sensitivity even at low flows'} />
                            <ProductAdvantages number={3} title={'Modern Technologies'} description={'Suitable for potable water with special glass with high resistance to pressure and impact.'} />
                            <ProductAdvantages number={4} title={'Almost No Maintenance '} description={'3 years of warranty with all type of maintance, assures very high sensitivity.'} />
                            <ProductAdvantages number={5} title={'360° Rotating Lid'} description={'Suitable for potable water with special glass with high resistance high resistance to pressure and impact.'} />
                            <ProductAdvantages number={6} title={'Optical Reading'} description={'Single jet design assures very high sensitivity even at low flows'} />
                        </div>
                    </div>
                </div>
                <div className="productDimensions">
                    <p className='verticalText'>meter scheme</p>
                    <div className="productDimensionsBody">
                        <p className="siteTitle productDimensionsTitle">Dimensions</p>
                        <hr />
                        <img className='productDimensionsImage' src="images/Dimensions.svg" alt="Dimensions" />
                        <div className="productDimensionsList">
                            <ProductGraphItem title={'DN'} subtitle={'Nominal Diameter'} description={'15 mm & 20 mm'} />
                            <ProductGraphItem title={'H'} subtitle={'Total overall meter height'} description={'77 mm, 75 mm'} />
                            <ProductGraphItem title={'h'} subtitle={'Axis height'} description={'17 mm, 16.5 mm'} />
                            <ProductGraphItem title={'LB'} subtitle={'Length with connections'} description={'190 mm, 190 mm'} />
                            <ProductGraphItem title={'D'} subtitle={'Connecting Diameter'} description={'3/4 & 1, B'} />
                            <ProductGraphItem title={'L'} subtitle={'Length'} description={'110 mm, 110 mm'} />
                            <ProductGraphItem title={'B'} subtitle={'Width'} description={'80 mm, 76 mm'} />
                        </div>
                    </div>
                </div>
                <div className="productCurve">
                    <p className='verticalText'>meter scheme</p>
                    <div className="productCurveBody">
                        <p className="siteTitle productCurveBodyTitle">Performance Curve</p>
                        <hr />
                        <img className='productHeaderItemImage' src="images/curve.svg" alt="Product" />
                        <div className="productCurveList">
                            <ProductGraphItem title={'Q1'} subtitle={'Minimum Flowrate '} description={'0,025 & 0,0156 m3/h'} />
                            <ProductGraphItem title={'Q2'} subtitle={'Transitional Flowrate '} description={'0,040 & 0,025 m3/h '} />
                            <ProductGraphItem title={'Q3'} subtitle={'Permanent Flowrate'} description={'2,5 m3/h '} />
                            <ProductGraphItem title={'Q4'} subtitle={'Overload Flowrate '} description={'3,125 m3/h'} />
                        </div>
                    </div>
                </div>
                <img className='productImage' src="images/productImage.png" alt="product" />
                <div className="productDocumentation">
                    <p className='verticalText'>meter scheme</p>
                    <div className="productDocumentationBody">
                        <p className="siteTitle productDocumentationBodyTitle">Technical <span>Documentations</span></p>
                        <div className="productDocumentationList">
                            <FilesList link={'/'} date={'19 March, 2023'} description={'EAC Declaration of Conformity Electromagnetic'} />
                            <FilesList link={'/'} date={'04 April , 2027'} description={'EAC Conformity Electromagnetic'} />
                            <FilesList link={'/'} date={'19 March , 2023'} description={'EAC Declaration of Conformity Electromagnetic'} />
                        </div>
                    </div>
                </div>
                <div className="similarProducts">
                    <p className='verticalText'>you can also like</p>
                    <div className="similarProductsBody">
                        <p className="siteTitle similarProductsBodyTitle"><span>Similar</span> Products</p>
                        <div className="similarProductsList"> 
                            {
                                sampleProduct.map((item, index) => (
                                    <ProductsListItem  key={index} productData={item}/>
                                ))
                            }
                        </div>
                    </div>
                </div>
            </div>

            <div className="container">
                <ContactUs />
            </div>
            <Footer />
        </div>

    )
}

export default ProductPage;