import React from 'react';
import '../App.css'

function ProductGraphItem(props) {
    return (
        <div className='productGraphItem'>
            <p className="productGraphTitle">{props.title}</p>
            <p className="productGraphSubtitle">{props.subtitle}</p>
            <p className="productGraphDescription">{props.description}</p>
        </div>
    )
}
export default ProductGraphItem;