import React from 'react';
import '../App.css'
import Footer from '../components/Footer'

function Investors() {

    return (
        <div className='productsBody investorContainer'>
            <div className="container">
            <div className="productsBodyNavigation">
                <p><span>Home</span> <span className='linksSeperator'>/</span><span className='activeLink'>Investors</span></p>
            </div>
            <div className="investorContactBody">
                <p className="siteTitle productsBodyTitle">Contacts</p>

                <div className='investorBlock'>
                    <p className='verticalText'>visit us</p>
                    <div className='investorMainBlock'>
                        <div>
                            <p className='investorBlockDesc'>Interested in working together? Ok, don't be shy. Just fill out the form below. Don't like forms? That's ok, just <a href="mailto:Info@smarttechnomash.uz">email us</a>.</p>
                            <div className="investorInfoBlockItem">
                                <p className='investorBlockTitle'>Adresses:</p>
                                <p className='investorBlockSubtitle'>100016, Republic of Uzbekidtan, Tashkent city, Yashnabad district, Aviasiozlar-1, 119/2</p>
                            </div>
                            <div className="investorInfoBlockItem">
                                <p className='investorBlockTitle'>Phone Number:</p>
                                <p className='investorBlockSubtitle'>(994) 55 555 55 55</p>
                            </div>
                            <div className="investorInfoBlockItem">
                                <p className='investorBlockTitle'>Email:</p>
                                <p className='investorBlockSubtitle'><a href="mailto:Info@smarttechnomash.uz">Info@smarttechnomash.uz</a></p>
                            </div>
                            <div className="investorInfoBlockItem">
                                <p className='investorBlockTitle'>Working Hours:</p>
                                <p className='investorBlockSubtitle'>Monday - Friday, 9:00 - 18:00</p>
                            </div>
                        </div>
                        <img src="/images/contactImage.svg" alt="Contact" />
                    </div>

                </div>
            </div>
            </div>
            
            <Footer />
        </div>
    )
}

export default Investors;